const Game = function(){
    return this.$store.state.game
}

const GetMyData = function(){
    const myId = this.$store.state.user.id
    return this.$store.getters["game/GetMyData"](myId)[0]
} 

const GetMyCharacters = function(){
    const myId = this.$store.state.user.id
    return this.$store.getters["game/GetMyCharacters"](myId)
} 

export default {
    Game,
    GetMyData,
    GetMyCharacters
}